@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
*{
      padding: 0px;
      margin: 0px;
      box-sizing: border-box;
      font-family: 'Poppins', sans-serif;
}
a{
      text-decoration: none;
}
.maps{
      width: 100%;
      height: 400px;
}
.mapouter{
      position:relative;
      text-align:right;
      height:400px;
      width:100%;
}
.gmap_canvas {
      overflow:hidden;
      background:none !important;
      height:400px;
      width:100%;
}
.maps iframe{
      width: 100%;
      height: 100%;
      border: 0;
}
.find-container{
      position: relative;
      top: -46px;
      width: 100%;
      height: 160px;
      margin: auto;
}
.find-container .react-tabs__tab-list {
      border-bottom: none !important;
      margin-bottom: 2px !important;
      padding: 0;
}
.find-container .react-tabs__tab-panel {
      background-color: #fff;
      box-shadow: 0px 0px 2px 2px #ebebeb;
}
.find-container .react-tabs__tab{
      background-color: black;
      color: #fff;
      text-transform: uppercase;
      font-size: 16px;
      font-weight: 600;
      padding: 10px 14px;
}
.find-container .react-tabs__tab--selected {
      background: #fff;
      border-color: #e0e0e0 !important;
      color: black;
      border-radius: 0px !important;
}
.find-content{
      box-shadow: 0px 0px .7px .7px #e7e7e7;
      padding: 10px 10px 15px;
}
.find-content label{
      font-size: 12px;
      font-weight: 600;
      margin-bottom: 0px !important;
}
.find-content input::-webkit-input-placeholder{
      font-size: 12px;
}
.find-content input:-ms-input-placeholder{
      font-size: 12px;
}
.find-content input::placeholder{
      font-size: 12px;
}
.find-content .form-select{
      font-size: 13px;
      color: gray !important;
      padding : .48rem .75rem !important;
}
.find-content button{
      display: block;
      background-color: black;
      color: #fff;
      font-size: 18px;
      font-weight: 500;
      width: 90%;
      height: 45px;
      text-align: center;
      margin: 13px auto 0px;
      transition: all .4s ease-in-out;
}
.find-content button:hover{
      background-color: #fff;
      color: #000;
      border: 1px solid #000;
}

@media screen and (max-width: 767px) {
      .find-container {
            height: 370px;
      }
}
.jobs-header-details{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 8px;
}
.jobs-header-details > span{
      font-size: 14px;
      font-weight: 500;
}
.jobs-header-details > span b{
      font-size: 12px;
      color: #3c3cfd;
}
.jobs-sort{
      display: flex;
      align-items: center;
}
.jobs-sort h4{
      font-size: 15px;
      color: rgb(255, 90, 30);
      padding-left: 10px;
      margin-bottom: 0px !important;
}
.jobs-sort span{
      font-size: 14px;
      font-weight: 600;
}
.jobs-tabs{
      border: 1px solid #d4d4d4;
      height: 400px;
}
.jobs-tabs .react-tabs__tab-list {
      background-color: #ebebeb;
      padding: 10px 20px 0px;
      margin-bottom: 0px !important;
}
.jobs-tabs .react-tabs__tab {
      font-size: 16px;
      font-weight: 600;
      color: #616161;
}
.jobs-tabs .react-tabs__tab--selected {
      color: black !important;
}
.job-tab-content{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px;
      border-bottom: 1px solid #d1d0d0;
}
.job-tab-img{
      width: 15%;
}
.job-tab-img img{
      width: 45px;
      height: 45px;
      padding: 5px;
      border: .7px solid #cccccc;
      border-radius: 3px;
}
.job-tab-information{
      display: flex;
      justify-content: space-between;
      width: 230px;
}
.job-tab-data h2{
      font-size: 16px;
}
.job-tab-data > div{
      display: flex;
      justify-content: start;
}
.job-tab-data > div h6{
      font-size: 10px;
}
.job-tab-data > div h6:nth-child(1){
      color: #3ebff8;
      border-right: 1px solid #d1d1d1;
      padding-right: 10px;
}
.job-tab-data > div h6:nth-child(2){
      color: #b8b8b8;
      padding-left: 10px;
}
.job-tab-located span ,
.job-tab-timer span,
.job-tab-status span{
      font-size: 12px;
      color: #b8b8b8;
}
.job-tab-status span{
      text-transform: uppercase;
      display: block;
      width: 100px;
      height: 30px;
      line-height: 30px;
      font-weight: 600;
      text-align: center;
      border-radius: 3px;
}
.job-temporary span{
      background-color: #3ef12e;
      color: #fff;
}
.job-intern span{
      background-color: #fc1010;
      color: #fff;
}
.job-part span{
      background-color: #e6f516;
      color: #fff;
}
.job-freelance span{
      background-color: #0a9ffb;
      color: #fff;
}
.job-tab-dots{
      display: flex;
}
.job-tab-dots span{
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #1f1f1f;
      margin: 2px;
}
.load-more-list{
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0px 20px;
}
.load-more-list button{
      background-color: #e6e6e6;
      color: #000;
      border: 1px solid #e6e6e6;
      width: 100%;
      text-transform: uppercase;
      font-size: 16px;
      font-weight: 600;
}
.load-more-list button:hover{
      background-color: #fff;
      color: #3c3cfd;
      border: 1.5px solid #3c3cfd;
}


/* JOBS Accordion */
.jobs-searching{
      border: 1px solid #d3d3d3;
      height: 430px;
      position: relative;
}
.accordion-body {
      padding: .5rem 1.25rem;
}
.accordion-flush .accordion-item:last-child {
      border-bottom: 1px solid rgba(0,0,0,.125);
}
.accordion-button:not(.collapsed) {
      color: #000;
      background-color: #fff;
      box-shadow: inset 0 -1px 0 rgba(0,0,0,.125);
}
.accordion label{
      font-size: 12px;
      font-weight: 600;
      margin-bottom: 0px !important;
}
.accordion input::-webkit-input-placeholder{
      font-size: 12px;
}
.accordion input:-ms-input-placeholder{
      font-size: 12px;
}
.accordion input::placeholder{
      font-size: 12px;
}
.accordion .form-select{
      font-size: 13px;
      color: gray !important;
      padding : .48rem .75rem !important;
}
.accordion-header {
      /* padding: 0px 10px; */
}
.accordion-button {
      padding: 0px 20px!important;
      height: 45px;
      font-size: 16px;
      font-weight: 600;
      text-transform: uppercase;
      color: #212529;
}
.accordion-button span{
      width: 40px;
      height: 100%;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      border-right: 1px solid #bdbdbd;
      padding-right: 20px;
      margin-right: 20px;
}
.accordion-button span{

}
.jobs-searching > button {
      background-color: #000;
      color: #fff;
      width: 90%;
      margin: 5px 5%;
      position: absolute;
      bottom: 10px;
      transition: all .4s ease-in-out;
  }
.jobs-searching > button:hover{
      background-color: #fff;
      color: #000;
      border: 1px solid #000;
}
.accordion-button::after {
      flex-shrink: 0;
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg);
      width: 1rem;
      height: 1rem;
      background-size: 1rem;
      content: "";
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23212529'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
}
.accordion-button:not(.collapsed)::after {
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23212529'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
}


/* Mobile Screen */
@media screen and (max-width : 767px) {
      .find-container {
            height: 370px;
      }
      .jobs-container{
            height: 725px;
      }
      .jobs-header-details{
            flex-direction: column
      }
      .jobs-header-details > span {
            font-size: 15px;
            margin-bottom: 10px;
      }
      .jobs-tabs {
            height: 645px;
      }
      .jobs-tabs .react-tabs__tab-list {
            padding: 10px 0px 0px;
            display: flex;
            overflow: hidden;
        }
      .job-tab-content{
            flex-flow: row wrap;
            position: relative;
      }
      .job-tab-information{
            margin-top: 10px;
