/* feature-job-slide */
.feature-job-slide{
      display: flex;
      flex-direction: column;
      width: 100%;
}
.feature-job-slide .featured-slide-bg{
      width: 100%;
      height: 170px;
      position: relative;
}
.featured-slide-bg img{
      width: 100%;
      height: 100%;
}
.featured-slide-bg::before{
      content: 'TEMPORARY';
      position: absolute;
      width: 120px;
      height: 25px;
      bottom: 10px;
      right: 10px;
      line-height: 25px;
      background-color: #22e943;
      color: #fff;
      z-index: 2;
      text-align: center;
      font-size: 15px;
      font-weight: 600;
}
.featured-slide-bg::after{
      content: '';
      position: absolute;
      inset: 0;
      background-color: #2e2d2d6c;
      z-index: 1;
}
.feature-job-details{
      display: flex;
}
.feature-job-img{
      width: 15%;
      padding: 22px 10px;
}
.feature-job-img img{
      width: 50px;
      height: 50px;
      border: .7px solid #cccccc;
      border-radius: 3px;
}
.feature-job-data{
      width: 80%;
      padding: 20px 20px;
}
.feature-job-info{
      display: flex;
      flex-direction: column;
}
.feature-job-info h2{
      font-size: 18px;
}
.feature-job-info > div{
      display: flex;
}
.feature-job-info > div h6{
      font-size: 14px;
}
.feature-job-info > div h6:nth-child(1){
      color: #3ebff8;
      border-right: 1px solid #d1d1d1;
      padding-right: 15px;
}
.feature-job-info > div h6:nth-child(2){
      color: #b8b8b8;
      padding-left: 15px;
}
.feature-job-desc{
      color: #a5a5a5;
      font-size: 14px;
      width: 100%;
      text-align: justify;
}
.feature-job-located{
      display: flex;
}
.feature-job-located span{
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 11px;
      color: #a5a5a5;
