.Candidates{
      width: 100%;
      height: 700px;
      position: relative;
}
.candidates-header{
      position: relative;
      background-color: #4f52d3;
      width: 100%;
      height: 400px;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 60px;
}
.candidates-find ,
.candidates-title ,
.candidates-desc
{
